body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    /* width:70vh; */
    float: left;
}
#imgi{
    width: 100vh;
    margin-top: 5vh;
}

.jumbotron {
    background-image: linear-gradient(
       

        #aaffa9
        ,
        #11ffbd
        );
    border: 1px solid #2980B9 ;
    border-radius: 20px;
    margin-left: 4vh;
    box-shadow: 10px 15px rgba(0,0,0,0.1);
    
}

.nav-item .welcomebtn {
 /* background-image: linear-gradient(rgb(183, 0, 255), rgb(243, 191, 240)); */
 background-image: linear-gradient(#b2fefa
 ,
 #0ed2f7);
 margin: 40px 10px 20px 10px;
 width: 25vh;
 margin-left: 40vh;
 padding: 10px;
 border-radius: 40px;
 box-shadow: 5px 10px rgba(0,0,0,0.1);
}
 .nav-link {
     text-align: center;
     color: white;
     font-weight: 600;
 }

