    .container {
        /* width:60vh; */
       
    }
    #bg{
        /* width: 50vh;
        margin-left: 20vh;
        margin-top: 5vh; */
       
    }
    #imgi{
        width: 63vh;
      float: left;
       
    }
    .nav-item div {
        /* background-image: linear-gradient(rgb(183, 0, 255), rgb(243, 191, 240)); */
        background-image: linear-gradient(#a8ff78,#78ffd6);
        margin: 40px 10px 20px 10px;
        width: 25vh;
        margin-left: 20vh;
        padding: 10px;
        border-radius: 40px;
       }
  }

