  #in{
    background-color: #e9b7f0;
    border: 0;
    width: 200px;
    height: 50px;
    padding: 0 20px;
    margin: 20px 20px 20px 90px;
    border-radius: 10px;
    color: rgb(0, 0, 0);
  }
  #bt{
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: rgb(188, 28, 228);
  }

  @media (max-width: 767.98px) { 
  
    #App {
      margin-left: 0vh;
    }
   
    #listi{
        width: 60vh;
        height: 50vh;
        margin-left: 0vh;
        margin-top: 5vh;
       
    }
  }
.list{
    color: #fff;
    
    width: 250px;
    margin: 30px 15vh;
    height: 50px;
    background-color: #e9b7f0;
    border-radius: 5px;
    
}
.list p{
    padding: 10px;
    position: relative;
    transition: ease-in-out;
    color: #e385ee;
   
}
.in{
    background-color: #e9b7f0;
    border: none;
}
